'use strict';
angular.module('core.utils').controller('FooterCtrl', /*@ngInject*/ function controller($scope, $timeout, $mdDialog, $state, Contact, Share) {
    var vm = this;

    // 
    // Contact
    // 
    vm.contact = new Contact.service();
    vm.submitContact = submitContact;

    vm.openForm = openForm;

    //
    // Esconder/mostrar botões ao rolar
    //
    var scrollElem = $("md-content.layout-content"),
        btnTop = scrollElem.find('#btn-speed-top'),
        btnContact = scrollElem.find('#btn-speed-contact'),
        timer = false;


    scrollElem.on('scroll', function() {
        var scroll = $(this);

        // 
        // Cancelar timeout anterior
        // 
        $timeout.cancel(timer);

        timer = $timeout(function() {
            // 
            // Posição da janela, div de contato e distancia entre os dois
            // 
            var position = scroll.scrollTop(),
                divContactPos = scrollElem.find('#contato').position().top;

            // console.log('position', position);
            // console.log('divContactPos', divContactPos);
            
            // 
            // Esconder mostrar botão top de acordo com posição do topo
            // 
            if (position > 100) {
                btnTop.fadeIn();
            } else {
                btnTop.fadeOut();
            }

            // 
            // Esconder / mostrar botão de contato de acordo com a distancia
            // 
            if (divContactPos < 350) {
                btnContact.fadeOut();
            } else {
                btnContact.fadeIn();
            }
        }, 500);
    });

    // 
    // Code
    // 
    function submitContact() {

        // Send contact to server with recaptcha
        vm.contact.create().then(success, fail);

        function success(response) {
            $state.go('app.home.obrigado');

            // console.log('response', response);
            // console.log('sucesso');
        }

        function fail(response) {
            // console.log('response', response);
            // console.log('falhou');
        }
    }

    function openForm(ev) {
        $mdDialog.show({
            controller: DialogController,
            templateUrl: 'app/home/indicacao-form.tpl.html',
            parent: angular.element(document.body),
            targetEvent: ev,
            clickOutsideToClose: true
        });
    }

    function DialogController($scope, $mdDialog, Share) {
        $scope.hide = hide;
        $scope.share = new Share.service();
        $scope.submit = submit;

        function hide() {
            $mdDialog.hide();
        };

        function submit() {
            // Send contact to server with recaptcha
            $scope.share.create().then(success, fail);

            function success(response) {
                // 
                // Fechar 
                // 
                hide();

                // console.log('response', response);
                // console.log('enviado com sucesso');
            }

            function fail(response) {
                // console.log('response', response);
                // console.log('falhou');
            }
        }
    }

    function submitNews() {

        // Send contact to server with recaptcha
        vm.news.create().then(success, fail);

        function success(response) {
            //
            // Enviar para página tabela de valores
            // 
            $state.go('app.valores');

            $scope.$on('$stateChangeSuccess', function(ev) {
                $timeout(function() {
                    $("md-content.layout-content").animate({
                        scrollTop: 0
                    }, 100);
                }, 1000)
            });

            // console.log('response', response);
            // console.log('enviado com sucesso');
        }

        function fail(response) {
            // console.log('response', response);
            // console.log('falhou');
        }
    }
});
(function() {
    'use strict';
    angular.module('core.utils').service('Busy', /*@ngInject*/ function($page) {

        this.service = service;

        function service(params) {
            //
            // Default Params
            // 
            this._status = {};

            //
            // Instantiate
            // 
            params = params ? params : {};
            angular.extend(this, params);

        };

        this.service.prototype.status = status;
        this.service.prototype.start = start;
        this.service.prototype.end = end;

        function status(method, value) {
            if (value || value === false) return this._status[method] = value;
            else return this._status[method];
        }

        function start(method, dontChangePage) {
            //
            // Are we busy? return true so consuming code can stop
            // 
            if (this.status(method)) return true;

            //
            // We're not busy, make it
            // 
            this.status(method, true);

            //
            // Enable page load?
            // 
            if (!dontChangePage) $page.load.init();

            //
            // Return false so consuming code can continue
            // 
            return false;
        }

        function end(method, dontChangePage) {
            // Disable status
            this.status(method, false);

            // Disable page loading?
            if (!dontChangePage) $page.load.done();
        }
    });
})();
