(function() {
    'use strict';
    angular.module('core.contact').service('Contact', /*@ngInject*/ function($http, $page, $state, $q, api, Busy) {

        var url = api.url + 'api/contacts/';
        // var url = 'http://premiereserver.inocoopes.com.br/api/contacts/';
        this.service = service;

        function service(params) {
            //
            // Default Params
            // 
            this.busy = new Busy.service();

            //
            // Instantiate
            // 
            params = params ? params : {};
            angular.extend(this, params);

        };

        this.service.prototype.create = create;
        this.service.prototype.formStatus = formStatus;
        this.service.prototype.subjects = subjects;
        this.service.prototype.negativePromise = negativePromise;

        function create() {
            // Are we busy?
            if (this.busy.start('create')) return this.negativePromise();

            // 
            // Validar email
            // 
            if (!this.email || !validateEmail(this.email)) {
                $page.toast('Insira um email válido!');
                this.busy.end('create');

                return this.negativePromise();
            }

            //
            // Limpar o model e separar outros dados
            // 
            var model = cleanModel(this);

            return $http
                .post(url, {
                    model: model
                })
                .success(
                    //
                    // success
                    //
                    function(response) {
                        $page.toast('Contato enviado com sucesso, responderemos em breve!');
                        this.busy.end('create');

                        // Limpar o form
                        this.name = '';
                        this.email = '';
                        this.phone = '';
                        this.message = '';
                        this.type = '';
                        this.comingFrom = '';

                        return response;
                    }.bind(this))
                .error(
                    //
                    // fail
                    //
                    function(response) {
                        $page.toast(response && response.error ? response.error : 'não foi possível enviar a mensagem, tente novamente!');
                        this.busy.end('create');

                        return response;
                    }.bind(this));
        }

        function formStatus(method, invalid) {
            return (this.busy.status(method) || invalid);
        }

        function subjects() {
            return [{
                value: "Contato",
                name: "Contato"
            }, {
                value: "Proposta",
                name: "Proposta"
            }, {
                value: "Sugestão",
                name: "Sugestão"
            }, {
                value: "Outros",
                name: "Outros"
            }];
        }

        function negativePromise() {
            // Start promise
            var defer = $q.defer();

            // Reject promise
            defer.reject(false);

            // Return promise
            return defer.promise;
        }

        function cleanModel(model) {
            if (!model) return model;

            var newModel = $.extend({}, model);

            newModel.busy = undefined;

            var clone = $.extend({}, newModel);
            return clone;
        }

        function validateEmail(email) {
            var re = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i;
            return re.test(email);
        }
    });
})();
